// Gatsby supports TypeScript natively!
import React from "react"
import { PageProps, Link, graphql } from "gatsby"
import styled from "@emotion/styled"
import { css } from "@emotion/core"

import Layout from "../components/layout"
import SEO from "../components/seo"
import { rhythm } from "../utils/typography"
import Waflash from "../components/waflash"

type Data = {
  site: {
    siteMetadata: {
      title: string
    }
  }
  allMarkdownRemark: {
    edges: {
      node: {
        excerpt: string
        frontmatter: {
          title: string
          date: string
          description: string
          thumbnail: {
            publicURL: string
          }
          swf: {
            publicURL: string
          }
        }
        fields: {
          slug: string
        }
      }
    }[]
  }
}

const Card = styled.article`
  position: relative;
  display: grid;
  background-color: white;
  border-radius: 9px;
  border: 1px solid #d3d6e7;
  box-shadow: 0 0 30px #d5d5d5;
  overflow: hidden;
  line-height: 1.5;
  &:hover {
    color: #381696;
  }
`
const CardTitle = styled.div`
  font-size: 0.9rem;
  margin-bottom: 0.3rem;
  //margin-block-start: 0.83em;
  margin-block-end: 0.3em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
`

const CardDesc = styled.div`
  font-size: .6rem;
  font-weight: 100;
  margin-botton: 0;
  margin-block-end: 0.3em;
`

const CardLikes = styled.div`
  position: absolute;
  top: 125px;
  right: 7px;
  font-size: 0.8rem;
  padding: 1px 4px;
  background-color: black;
  color: white;
`

const PostCard = (props) => {
  const node = props.post;
  const title = node.frontmatter.title || node.fields.slug
  const o = node.frontmatter;
  return (
    <Card>
      <a style={{ boxShadow: `none` }} href={node.fields.slug}>
        <img style={{ marginBottom: 0 }} src={o.thumbnail.publicURL} width={320} height={150} />
      </a>
      <CardLikes><span>100</span></CardLikes>
      <div style={{ paddingLeft: "8px", marginTop: "0" }}>
        <CardTitle>
          <a style={{ boxShadow: `none` }} href={node.fields.slug}>
            {title}
          </a>
        </CardTitle>
        <CardDesc>by {o.author}, from {o.from}</CardDesc>
      </div>
    </Card>
  )
}

const BlogIndex = ({ data, location }: PageProps<Data>) => {
  const siteTitle = data.site.siteMetadata.title
  const posts = data.allMarkdownRemark.edges

  const Posts = posts.map(({node}) => <PostCard key={node.fields.slug} post={node}></PostCard>)

  

  return (
    <Layout location={location} title={siteTitle}>
      <SEO title="WebAssembly Flash" />

      <h3 style={{ textAlign: "center", lineHeight: '1.5' }}>Waflash is a WebAssembly AS2/AS3 Flash compatible player<br/>without plugin.</h3>

      <Waflash width={'100%'} height={'480px'} />
      
      <h2>Featured Demo</h2>

      <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr 1fr", gridGap: "12px", marginTop: "12px" }}>
        {Posts}
      </div>

    </Layout>
  )
}

export default BlogIndex

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
            description
            author
            authorUrl
            from
            fromUrl
            swf {
              publicURL
            }
            thumbnail {
              publicURL
            }
          }
        }
      }
    }
  }
`
